function generateSrcSet(photoProps) {
    var sources = [];
    ['m', 'n', 'z', 'c', 'b', 'h', 'k', 'o'].forEach(function(size) {
        if (photoProps['url_' + size]) {
            sources.push(photoProps['url_' + size] + ' ' + photoProps['width_' + size] + 'w');
        }
    });

    return sources.join(', ');
}

exports.generateSrcSet = generateSrcSet;


