function fetchImages(searchTerm, pageNum, callback) {
    if (!searchTerm || typeof searchTerm !== 'string') {
        return new Error('Invalid search term');
    }

    var onResolve = callback || function(err, res) { console.log(err, res); }; // eslint-disable-line no-console

    var xhr = new XMLHttpRequest();
    var url = formUrl(searchTerm, pageNum);
    xhr.open('GET', url);
    xhr.send();

    xhr.onreadystatechange = function() {
        if (xhr.readyState === 4) {
            if (xhr.status === 200) {
                var responseText = JSON.parse(xhr.responseText);
                // this second conditional has to do with flickr's api responding with an error
                // despite the status being 200
                if (responseText.stat === 'ok') {
                    onResolve(null, responseText);
                } else {
                    onResolve('Flickr: ' + responseText.code);
                }
            } else {
                onResolve(xhr.status);
            }
        }
    };
}

exports.fetchImages = fetchImages;


// "private" functions

function formUrl(searchTerm, pageNum) {
    var url = 'https://api.flickr.com/services/rest/?';
    url += 'method=flickr.photos.search';
    url += '&format=json';
    url += '&nojsoncallback=1';
    url += '&api_key=8290f44a4d72c91fe3aad3afd2016f6d'; // yes, I know I know
    url += '&per_page=25';
    url += '&page=' + pageNum;
    url += '&extras=url_t,url_m,url_n,url_z,url_c,url_b,url_h,url_k,url_o';
    url += '&text=' + searchTerm;

    return url;
}


