function generatePhotoUrl(photoProps) {
    var url = 'https://farm' + photoProps.farm + '.staticflickr.com/';
    url += photoProps.server + '/';
    url += photoProps.id + '_' + photoProps.secret + '.jpg';
    return url;
}

exports.generatePhotoUrl = generatePhotoUrl;


