var fetchImages = require('./utils/fetchImages').fetchImages;
var generatePhotoUrl = require('./utils/generatePhotoUrl').generatePhotoUrl;
var generateSrcSet = require('./utils/generateSrcSet').generateSrcSet;

var FlickrSearchApp = {
    initialize: function() {
        // holds properties that aren't directly evident when reading code
        this.state = {
            photos: [],
            pageNum: 1,
            lightboxIndex: 0,
            isFetchingPhotos: false
        };

        // holds elements that need to be referenced multiple times
        this.elements = {
            imageResults: document.getElementById('image-results'),
            searchSubmitButton: document.getElementById('flickr-search-submit'),
            showMoreButton: document.getElementById('show-more-photos'),
            lightbox: document.getElementById('lightbox'),
            lightboxPhotoWrapper: document.getElementById('lightbox-photo-wrapper'),
            lightboxCaption: document.getElementById('lightbox-caption'),
            lightboxProgress: document.getElementById('lightbox-which-photo'),
            errorMessage: document.getElementById('error-message')
        };

        this.addEventListeners();
    },

    cleanForFreshSearch: function() {
        this.state.photos = [];
        this.state.pageNum = 1;
        this.elements.imageResults.innerHTML = '';
        this.elements.errorMessage.classList.add('is-hidden');
        this.elements.showMoreButton.classList.add('is-hidden');
    },

    addEventListeners: function() {
        var searchForm = document.getElementById('flickr-search-form');
        var searchInput = document.getElementById('flickr-search-input');

        var setFetchingPhotosState = this.setFetchingPhotosState.bind(this);
        var cleanForFreshSearch = this.cleanForFreshSearch.bind(this);
        var handleResponse = this.handleResponse.bind(this);
        var openLightbox = this.openLightbox.bind(this);
        var closeLightbox = this.closeLightbox.bind(this);
        var changeLightBoxIndexBy = this.changeLightBoxIndexBy.bind(this);

        var that = this; // for non-function values

        // submitting the search form
        searchForm.addEventListener('submit', function(e) {
            e.preventDefault();

            if (!that.state.isFetchingPhotos && searchInput.value !== '') { // only allow one fetch at a time
                cleanForFreshSearch();
                that.searchTerm = searchInput.value;
                setFetchingPhotosState(true);
                fetchImages(searchInput.value, that.state.pageNum, handleResponse);
            }
        });

        // clicking the show more button
        this.elements.showMoreButton.addEventListener('click', function() {
            setFetchingPhotosState(true);
            fetchImages(that.searchTerm, that.state.pageNum, handleResponse);
        });

        // clicking a thumbnail
        this.elements.imageResults.addEventListener('click', function(e) {
            if (e.target && e.target.classList.contains('flickr-thumbnail-img')) {
                that.state.lightboxIndex = parseInt(e.target.parentElement.getAttribute('data-index'));
                openLightbox();
            }
        });

        // clicking lightbox elements
        this.elements.lightbox.addEventListener('click', function(e) {
            // I'd rather use one listener instead of multiple
            if (e.target) {
                if (e.target.classList.contains('lightbox-close')) {
                    closeLightbox();
                } else if (e.target.classList.contains('lightbox-previous-image')) {
                    changeLightBoxIndexBy(-1);
                } else if (e.target.classList.contains('lightbox-next-image')) {
                    changeLightBoxIndexBy(1);
                }
            }
        });

        // listener for lightbox controls
        window.addEventListener('keyup',function(e) {
            if (!that.elements.lightbox.classList.contains('is-hidden')) { // lightbox is open
                switch (e.keyCode) {
                case 27:
                    closeLightbox();
                    break;
                case 37:
                    changeLightBoxIndexBy(-1);
                    break;
                case 39:
                    changeLightBoxIndexBy(1);
                }
            } else if (e.keyCode === 13 && document.activeElement.classList.contains('flickr-thumbnail-img')) {
                that.state.lightboxIndex = parseInt(e.target.parentElement.getAttribute('data-index'));
                openLightbox();
            }
        });
    },

    setFetchingPhotosState: function(newState) {
        this.state.isFetchingPhotos = newState;
        this.elements.showMoreButton.disabled = newState;
        this.elements.searchSubmitButton.disabled = newState;
    },

    changeLightBoxIndexBy: function(indexChange) {
        var newIndex = this.state.lightboxIndex + indexChange;
        var numPhotos = this.state.photos.length;

        if (newIndex < 0) {
            newIndex = numPhotos - 1;
        } else if (newIndex >= numPhotos) {
            newIndex = 0;
        }

        this.state.lightboxIndex = newIndex;
        this.showLightboxPhoto();
    },

    openLightbox: function() {
        this.elements.lightbox.classList.remove('is-hidden');
        document.body.classList.add('has-lightbox-open');
        this.showLightboxPhoto();
    },

    showLightboxPhoto: function() {
        this.setCaption();
        this.setLightboxProgress();

        this.elements.lightboxPhotoWrapper.classList.add('is-loading');
        this.elements.lightboxPhotoWrapper.innerHTML = ''; // removes old photo if present

        var photoProps = this.state.photos[this.state.lightboxIndex];
        var imgSrc = generatePhotoUrl(photoProps);

        var that = this;

        var lightboxPhoto = new Image();
        lightboxPhoto.setAttribute('alt', photoProps.title); // yay alt
        var srcset = generateSrcSet(photoProps);
        if (srcset) {
            lightboxPhoto.setAttribute('srcset', srcset);
        }
        lightboxPhoto.onload = function() {
            that.elements.lightboxPhotoWrapper.classList.remove('is-loading');
            lightboxPhoto.style.opacity = 0;
            that.elements.lightboxPhotoWrapper.appendChild(lightboxPhoto);
            window.getComputedStyle(lightboxPhoto).opacity;
            lightboxPhoto.style.opacity = 1;
        };

        lightboxPhoto.src = imgSrc;
    },

    setCaption: function() {
        var caption = this.state.photos[this.state.lightboxIndex].title;
        this.elements.lightboxCaption.innerText = caption;
    },

    setLightboxProgress: function() {
        var numPhotos = this.state.photos.length; // checking this here in case user is browsing when photos are added
        this.elements.lightboxProgress.innerText = 'Image ' + (this.state.lightboxIndex + 1) + ' of ' + numPhotos;
    },

    closeLightbox: function() {
        this.elements.lightbox.classList.add('is-hidden');
        document.body.classList.remove('has-lightbox-open');
    },

    handleResponse: function(err, res) {
        this.setFetchingPhotosState(false);

        if (err) {
            return this.setErrorMessage('Sorry, there was an error. Mind trying a new search?');
        } else if (res.photos.photo.length === 0) {
            return this.setErrorMessage("Sorry, there weren't any results for that. Mind trying a new search?");
        }

        this.addPhotos(res.photos);

        // this checks to see that there are more images available to search for
        if (res.photos.page < res.photos.pages) {
            this.state.pageNum++;
            this.elements.showMoreButton.classList.remove('is-hidden');
        } else {
            this.elements.showMoreButton.classList.add('is-hidden');
        }
    },

    setErrorMessage: function(message) {
        this.elements.errorMessage.innerText = message;
        this.elements.errorMessage.classList.remove('is-hidden');
    },

    addPhotos: function(photos) {
        for (var i = 0; i < photos.photo.length; i++) {
            var photo = photos.photo[i];
            this.addThumbnail(photo);
            this.state.photos.push(photo);
        }
    },

    addThumbnail: function(photo) {
        var thumbnailEl = document.createElement('div');
        thumbnailEl.className = 'flickr-thumbnail';
        thumbnailEl.setAttribute('data-index', this.state.photos.length);
        thumbnailEl.style.height = photo.height_t + 'px';
        thumbnailEl.style.width = photo.width_t + 'px';
        this.elements.imageResults.appendChild(thumbnailEl);

        var thumbImage = new Image();
        thumbImage.setAttribute('alt', 'Thumbnail for ' + photo.title); // yay alt
        thumbImage.classList.add('flickr-thumbnail-img');
        thumbImage.setAttribute('tabindex', this.state.photos.length + 1); // not the best practice
        thumbImage.onload = function() {
            thumbImage.style.opacity = 0;
            thumbnailEl.appendChild(thumbImage);
            window.getComputedStyle(thumbImage).opacity;
            thumbImage.style.opacity = 1;
        };

        thumbImage.src = photo.url_t;
    }
};

exports.default = FlickrSearchApp;


